import { v as validate_get_admin_body } from "../../../../chunks/endpoint_utils.js";
import { z } from "zod";
import { p as prisma_client } from "../../../../chunks/prisma_client.js";
import { j as json } from "../../../../chunks/index2.js";
const upload_response_schema = z.object({
  fileId: z.string(),
  fileType: z.string(),
  url: z.string()
});
async function upload_image(id, image) {
  const url = "https://upload.imagekit.io/api/v1/files/upload";
  const form_data = new FormData();
  form_data.append("file", image);
  form_data.append("fileName", id);
  const response = await fetch(url, {
    method: "POST",
    headers: {
      Authorization: "Basic " + process.env.IMAGEKIT_PRIVATE_KEY
    },
    body: form_data
  }).then((response2) => response2.json());
  return upload_response_schema.parse(response);
}
const POST = async ({ request }) => {
  const { id, image } = await validate_get_admin_body(
    request,
    z.object({
      id: z.string(),
      image: z.string()
    })
  );
  const response = await upload_image(id, image);
  const url = response.url;
  await prisma_client.imageLink.upsert({
    where: {
      id
    },
    create: {
      id,
      image_url: url
    },
    update: {
      image_url: url
    }
  });
  return json({
    image_url: url
  });
};
export {
  POST
};
